/**
 * src/components/ChatInput.tsx
 * Provides the chat input field component with send button
 * Used by: src/app/page.tsx
 */

import React, { useState } from 'react';

// ===== TYPES =====
interface ChatInputProps {
  onSend: (message: string) => void;
  placeholder?: string;
}

// ===== COMPONENT =====
export default function ChatInput({ onSend, placeholder = 'How can I help you today?' }: ChatInputProps) {
  const [message, setMessage] = useState('');

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (message.trim()) {
      onSend(message);
      setMessage('');
    }
  };

  return (
    <div className="w-full max-w-4xl mx-auto relative">
      <form onSubmit={handleSubmit} className="relative">
        <div className="rounded-full bg-white dark:bg-black border border-gray-200 dark:border-gray-800 shadow-sm flex items-center p-2">
          <textarea
            value={message}
            onChange={(e) => setMessage(e.target.value)}
            placeholder={placeholder}
            className="flex-1 bg-transparent resize-none focus:outline-none px-3 py-2 h-10 min-h-10 max-h-[200px] overflow-y-auto"
            rows={1}
            onKeyDown={(e) => {
              if (e.key === 'Enter' && !e.shiftKey) {
                e.preventDefault();
                handleSubmit(e);
              }
            }}
          />
          <button
            type="submit"
            disabled={!message.trim()}
            className="rounded-full p-2 text-white bg-[#d9b5a7] hover:bg-[#c9a594] disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
            aria-label="Send message"
          >
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" className="w-5 h-5 rotate-90">
              <path d="M3.478 2.404a.75.75 0 0 0-.926.941l2.432 7.905H13.5a.75.75 0 0 1 0 1.5H4.984l-2.432 7.905a.75.75 0 0 0 .926.94 60.519 60.519 0 0 0 18.445-8.986.75.75 0 0 0 0-1.218A60.517 60.517 0 0 0 3.478 2.404Z" />
            </svg>
          </button>
        </div>
      </form>
    </div>
  );
} 